.spinner .background {
  display: flex;
  width: 100%;
  height: 100vh;
  align-items: center;
  flex-flow: row wrap;
  justify-content: center; }

.spinner .animated-part {
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
  border: 8px solid #f3f3f3;
  border-top: 8px solid #754ef9;
  border-radius: 50%; }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
